@import '../../styles/animations.scss';

.ProfilePickerList {
  @include entry-animation;

  &__title {
    font-size: 40px;
    margin-bottom: 10px;
    text-align: center;
  }

  &__description {
    color: rgb(172, 172, 172);
    font-size: 14px;
    text-align: center;
  }

  &__pickers {
    align-items: center;
    display: flex;
    flex-direction: column;
    margin-top: 50px;

    @media screen and (min-width: 768px) {
      flex-direction: row;
      justify-content: space-between;
    }
  }
}
